import { LitElement, html } from 'lit-element';

/**
 * `detroit-btn`
 * `Simple button component`
 *
 * @microcopy - language worth noting:
 *  -
 *
 * @customElement
 * @lit-html
 * @lit-element
 * @demo demo/index.html
 */
class DetroitBtn extends LitElement {
  
  // render function
  render() {
    return html`
<style>:host {
  display: block; }

:host([hidden]) {
  display: none; }
</style>
<slot></slot>
<div>${this.type}</div>
<div>${this.style}</div>`;
  }

  // properties available to the custom element for data binding
  static get properties() {
    return {
  "type": {
    "name": "type",
    "type": "String",
    "value": "solid",
    "reflectToAttribute": true,
    "observer": "_typeChanged"
  },
  "style": {
    "name": "style",
    "type": "String",
    "value": "color-1",
    "reflectToAttribute": true,
    "observer": "_styleChanged"
  }
};
  }

  /**
   * Store the tag name to make it easier to obtain directly.
   * @notice function name must be here for tooling to operate correctly
   */
  tag() {
    return "detroit-btn";
  }

  // life cycle
  constructor() {
    super();
    
    this.tag = DetroitBtn.tag;
    // map our imported properties json to real props on the element
    // @notice static getter of properties is built via tooling
    // to edit modify src/detroit-btn-properties.json
    let obj = DetroitBtn.properties;
    for (let p in obj) {
      if (obj.hasOwnProperty(p)) {
        if (this.hasAttribute(p)) {
          this[p] = this.getAttribute(p);
        }
        else {
          this.setAttribute(p, obj[p].value);
          this[p] = obj[p].value;
        }
      }
    }
  }
  /**
   * life cycle, element is afixed to the DOM
   */
  connectedCallback() {
    super.connectedCallback();
    
  }
  // static get observedAttributes() {
  //   return [];
  // }
  // disconnectedCallback() {}

  // attributeChangedCallback(attr, oldValue, newValue) {}
    // Observer type for changes
              _typeChanged (newValue, oldValue) {
                if (typeof newValue !== typeof undefined) {
                  console.log(newValue);
                }
              }

  // Observer style for changes
              _styleChanged (newValue, oldValue) {
                if (typeof newValue !== typeof undefined) {
                  console.log(newValue);
                }
              }


}
customElements.define("detroit-btn", DetroitBtn);
export { DetroitBtn };
